.main-slider__wrapper {
  padding: 0 9rem;
  margin-bottom: 8rem;
}

.main-slider__img,
.main-slider__sub-img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-slider__img {
  display: block;
}

.main-slider__sub-img {
  display: none;
}

.main-slider {

  .swiper-button-prev:after,
  .swiper-button-next:after {
    color: $white;
    font-size: 3rem;
  }

  .swiper-button-next,
  .swiper-button-prev {
    background-color: rgba(255, 255, 255, .14);
    border-radius: 50%;
    width: 6rem;
    height: 6rem;

    transition: background-color .3s ease;

    &:hover {
      background-color: rgba(255, 255, 255, .2);
    }
  }

  .swiper-pagination {
    bottom: 1.5rem;
  }

  .swiper-pagination-bullet {
    width: 1rem;
    height: 1rem;
    background-color: $white;
  }
}

@media (max-width: 1750px) {
  .main-slider__wrapper {
    padding: 0 4.5rem 0 9rem;
  }
}

@media (max-width: 1540px) {
  .main-slider__wrapper {
    padding: 0 2rem 0 9rem;
  }
}

@media (max-width: 1023px) {
  .main-slider__wrapper {
    padding: 0;
  }

  .main-slider {

    .swiper-pagination {
      display: none;
    }

    .swiper-button-prev:after,
    .swiper-button-next:after {
      color: $white;
      font-size: 2rem;
    }

    .swiper-button-next,
    .swiper-button-prev {
      width: 5rem;
      height: 5rem;
    }
  }
}

@media (max-width: 479px) {
  .main-slider {

    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
}

@media (max-width: 439px) {
  .main-slider__img {
    display: none;
  }

  .main-slider__sub-img {
    display: block;
  }
}